<template>
  <v-container fill-height>
    <v-row>
      <v-col cols="12">
        <v-row align="center" justify="center">
          <div class="mb-16 text-h3 font-weight-light black--text">Login</div>
        </v-row>
        <v-row align="center" justify="center">
          <v-card class="pa-8" width="500">
            <v-form ref="loginForm" v-model="isFormValid">
              <v-text-field
                label="Username"
                type="text"
                v-model="user.username"
                :rules="this.usernameRules"
                required
              ></v-text-field>
              <v-text-field
                label="Password"
                type="password"
                v-model="user.password"
                :rules="this.passwordRules"
                required
              ></v-text-field>
              <div class="text-center">
                <v-btn
                  class="mb-8 success"
                  width="100%"
                  type="submit"
                  :disabled="!isFormValid || isLoading"
                  :loading="isLoading"
                  @click.prevent="login()"
                >Login</v-btn>
                <router-link :to="{name: 'Register'}">Register</router-link>
              </div>
            </v-form>
            <div class="h6 red--text" :hidden="errorMessage.length === 0">{{ errorMessage }}</div>
          </v-card>
        </v-row>
      </v-col>
    </v-row>
    <v-snackbar v-model="isSnackbarShown" :bottom="true" :timeout="5000">
      {{ snackbarMessageDisplayed }}
      <template v-slot:action="{ attrs }">
        <v-btn dark text v-bind="attrs" @click="isSnackbarShown = false">Tutup</v-btn>
      </template>
    </v-snackbar>
  </v-container>
</template>
<script>
import User from "../models/userModel";
import inputFormMixin from "../mixins/inputFormMixin";
import errorMixin from "../mixins/errorMixin";

export default {
  name: "Login",
  data: () => ({
    user: new User(),
    isFormValid: false,
    isLoading: false,
    isSnackbarShown: false,
    snackbarMessageDisplayed: "",
    errorMessage: "",
  }),
  computed: {
    isLoggedIn() {
      return this.$store.state.auth.status.isLoggedIn;
    },
    userRole() {
      return this.$store.state.auth.user.role;
    },
    isRedirectLogin() {
      return this.$route.params.isRedirectLogin;
    },
    snackbarMessage() {
      return this.$route.params.snackbarMessage;
    },
  },
  methods: {
    login: async function () {
      this.isLoading = true;
      try {
        let loginResponse = await this.$store.dispatch("auth/login", this.user);
        if (loginResponse.status === 200) {
          this.handleLoginRedirection(loginResponse.data.userData.role);
        }
      } catch (error) {
        this.errorMessage = this.getErrorMessage(error);
      }
      this.isLoading = false;
    },
    handleLoginRedirection(userRole) {
      if (userRole === "ADMIN" || userRole === "SUPER ADMIN") {
        this.$router.push("admin");
      } else {
        this.$router.push("user");
      }
    },
  },
  created() {
    if (this.isLoggedIn) {
      this.handleLoginRedirection(this.userRole);
    }

    if (this.isRedirectLogin) {
      this.snackbarMessageDisplayed = this.snackbarMessage;
      this.isSnackbarShown = true;
    }
  },
  mixins: [inputFormMixin, errorMixin],
};
</script>